.testimonials {
  overflow-x: hidden;
}
.carousel-container {
  background-color: green;
  /* display: flex; */
  display: inline-flex;
  flex-wrap: nowrap;
  gap: 1rem;
  overflow-x: hidden;
  transition: all 0.3s ease-out;
}

.carousel-item:last-child {
  padding-right: 1rem;
}
.carousel-item {
  background-color: gray;
  height: 15rem;
  width: 70vw;
  transition: all 0.3s ease-out;
}

.carousel-item-active {
  background: linear-gradient(0deg, #213645 -0.13%, rgba(0, 0, 0, 0) 100%), linear-gradient(90deg, #E54DE5 0%, #FFC800 100%);
  box-shadow: 0px 4px 4px 3px rgba(0, 0, 0, 0.1);
  border-radius: 15px;
}